import React from 'react';
import { connect } from 'react-redux';
import Part from '../part/part'
import { removeSection,setCollapseOpen } from '../../redux/actions/funnel.action'


function Section(props) {
    const { section, indexSection, removeSection,setCollapseOpen } = props
    // const sectionStyle = {

    //     minHeight:"200px"

    // }

    return (
        <>
            <div className="row no-gutters" style={section.settings.style}>
                <div className='col-12'>
                <span class="material-icons" style={{ zIndex: 3 }} onClick={() => removeSection(indexSection)}>
                    delete
                </span>
                <span class="material-icons" style={{ zIndex: 4 }} onClick={() => setCollapseOpen('section')} >
                    create
                    </span></div>
                {props.section.arrParts.map((item, i) => {
                    return (
                        <div key={i} className={`col-md-${12 / props.section.arrParts.length} p-0`}>
                            <Part part={item} indexSection={indexSection} indexPart={i}></Part></div>
                    )
                })}
            </div>
        </>
    )
}
export default connect(
    undefined,
    (dispatch) => {
        return {

            removeSection: function (indexSection) {
                dispatch(removeSection(indexSection))
            },
            setCollapseOpen: function (collapse) { dispatch(setCollapseOpen(collapse)) }

        }
    }
)
    (Section)
