import $ from "jquery";


export const getFromServer = ({ dispatch, getState }) => next => action => {
    if (action.type === 'GET_JSON')
        $.ajax({
            url: 'https://randomuser.me/api/?results=10&seed=abc',
            type: 'GET',
            success: function (data) {
                console.log(data);
            }
        })
        return next(action)
}

