import React from 'react';
import Element from '../element/element'
import { connect } from 'react-redux';
import './part.css'
import { useDrop } from 'react-dnd';
import { ItemTypes } from '../../ItemTypes'

function Part(props) {
    const { part, flagBorder, indexPart, indexSection,numElement } = props
    // const partStyle = {
    //     //   backgroundColor:props.part.settings.bgColor,
    //     height: "100%",
        

    // }
    const [{ canDrop, isOver }, drop] = useDrop({
        
        accept: ItemTypes.BOX,
        drop: () => ({ name: 'Drop', indexSection: indexSection, indexPart: indexPart }),
        collect: (monitor) => ({
            isOver: monitor.isOver(),
            canDrop: monitor.canDrop(),
            ///////////////
        //    elemntDrop:monitor.getClientOffset(),

        }),
    });
    console.log('options', {canDrop, isOver});



    return (
        <>
            <div ref={drop} className={`p-2 ${flagBorder ? "border_part" : ""}`} style={part.settings.style}>
                {part.arrElements.length>0&&  part.arrElements.map((item, i) => {
                    return (
                        <Element key={i} currentKey={i} element={item} part={indexPart} section= {indexSection} id={item.id} ></Element>
                    )
                })||
                <p>add elements</p>}
            </div>
        </>
    )
}
export default connect(
    (state) => {
        return {
            flagBorder: state.funnel.borderPart,
           
        }
    }
)(Part)
