import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';
import './widget.css'
import { useDrag } from 'react-dnd'
import { ItemTypes } from '../../ItemTypes'
import { addElement } from '../../redux/actions/funnel.action'
import element from '../element/element';
import { setName } from '../../redux/actions/user.action';


function Widget(props) {
    const { numElement } = props
    const [settings, setSettings] = useState({})
    const [indexSection, setIndexSection] = useState()
    const [indexPart, setIndexPart] = useState()
    const [changePosition, setChangePosition] = useState()

    const [name, setName] = useState()

    const [{ isDragging }, drag] = useDrag({
        item: { name: props.name, type: ItemTypes.BOX },
        collect: (monitor) => ({
            isDragging: !!monitor.isDragging()

        }),
        end: async (item, monitor) => {
            const dropResult = monitor.getDropResult();

            if (item && dropResult) {
                console.log(settings);
                setName(item.name)
                if (indexPart === dropResult.indexPart && indexSection === dropResult.indexSection)
                    props.addElement(name, props.tagWidget, indexSection, indexPart, numElement)
                else {
                    setIndexPart(dropResult.indexPart)
                    setIndexSection(dropResult.indexSection)
                    setChangePosition(!changePosition)
                    
                }
                // console.log(dropResult.elementDrop);
                // if (item.name == "Title") {
                //     setSettings({ ...settings, fontSize: '3rem',color:'red'}) 
                // }
                // else { 
                //     setSettings({ ...settings, fontSize: '1rem',color:'blue' })
                //  }

                // props.addElement(item.name, settings, dropResult.indexSection, dropResult.indexPart)
                //    if()
                // props.addElement(name,props.tagWidget, indexSection, indexPart,numElement)


            }
        }
    })

    useEffect(() => {
        // setSettings({tamar:"cohjkds"});
        if (name) {

            props.addElement(name, props.tagWidget, indexSection, indexPart, numElement)

        }
        // console.log('dd', settings)
    }, [changePosition])

    return (
        <div ref={drag} className="d-flex pl-1" >
            <div className="newEelement col-1 d-flex justify-content-center p-1 px-3 my-3 " >
                <span class="material-icons">
                    {props.icon}
                </span>

            </div>
            <div className="mt-3 ml-2 p">
                <p>{props.name}</p>
            </div>
        </div>
    )
}
export default connect(
    (state) => {
        return {
            numElement: state.funnel.num_elements
        }
    },
    (dispatch) => {
        return {
            addElement: function (newElement, value, section, part, numElement) { dispatch(addElement({ type: newElement, value: `<${value} style="text-align:center"> ${newElement}</${value}>`, section: section, part: part, id: numElement })) },
        }
    },

)(Widget)